*{
  margin: 0px;
  padding:0px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.nav-ul{
  padding: 5px;
  margin: 0px;
  background-color: skyblue;
}
.nav-ul li{
  display: inline-block;
  padding: 10px;
}

.nav-ul li a{
  text-decoration: none;
  color: #fff;
  padding: 5px;
  
}
.SignupBox{
  margin-left: 30%;

}

.signheading,.addProdHeading{
  margin-left: 18%;
}

.inputbox{
  display: block;
  border: 1px solid skyblue;
  padding: 10px;
  height: 25px;
  width: 350px;
  margin: 20px;
}

.submitbutton{
  background-color: skyblue;
  padding: 10px;
  height: 40px;
  width: 70px;
  border: 1px solid;
  color: rgb(36, 35, 35);
  cursor: pointer;
  margin-left: 20px;

}

.loginBox{
  margin-left: 30%;
}

.logHead{
  margin-left: 20px;
}

.logo{
  height: 50px;
  width:50px;
  border-radius: 50%;
  float: left;
  margin-left: 10px;
  margin-top: 2px;

}
.log-ul{
  text-align: right;
}

.box,.btn{
  margin-left: 18%;
}

.btn{
  width:150px;
  margin-bottom: 10px;
}

.invalid-input {
    color: red;
    
    margin-left: 18%;
    font-size: 12px;
    position: absolute;
    transform: translateY(-20px); /* Adjusts vertically for close positioning */
  }
  
.product-list{
  text-align: center;
  margin-top: 50px;
}

.product-list ul li{
  display: inline-block;
  width: 150px;
  border: 1px solid skyblue;
  padding: 5px;
}

.product-list ul{
  margin: 0px;
}
.search-product-key{
  height: 30px;
  width: 400px;
  padding: 5px;
  border: 2px solid skyblue;
  margin: 5px;
  margin-bottom: 10px;
}

.footer{
  position: absolute;
  bottom: 0px;;
  width: 100%;
  background-color: skyblue;
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
